import React from 'react';
import { Provider, themes, Button, Flex, FlexItem, Dropdown, Input, Header} from '@fluentui/react-northstar';


const styles={
    mainPadding:{padding:"2em"},
    mainSidePadding:{padding:"10px 2em"},
    mainPdf:{display:"flex", justifyContent:"center"},
    mainTable:{
        backgroundColor: "#fff",
        border: "1px solid #d8e2f0",
        borderRadius: "4px",
        boxShadow: "0 0 10px 0 rgba(19,29,41,.05)",
        position: "relative",
        padding: "0em 3em 2em"
    },
    backButton:{
        cusor:"pointer"
    }
}
class Config extends React.Component{
    constructor(){
        super();
        if(this.microsoftTeams)
            this.microsoftTeams.initialize();
        this.state ={
            apiKey:"",
            adminId:"",
            contacts:null,
            contact:null
        }
        this.handleChange = this.handleChange.bind(this)
        this.submit = this.submit.bind(this)
        this.getOptions = this.getOptions.bind(this)
        this.handleContact = this.handleContact.bind(this)
        this.save = this.save.bind(this)
    }
    componentDidMount(){
        this.microsoftTeams = window? window.microsoftTeams: null;
    }

    getOptions(){
        const {contacts} = this.state;
        return contacts.map(c=>({content:c.company_name, value:c.id}))
    }

    handleContact(e, value){
        
        this.setState({ contact: value.value })
    }

    handleChange(event) {
        this.setState({[event.target.name]: event.target.value});
    }

    async submit(e){
        e.preventDefault();
        const {apiKey, adminId, value} = this.state;
        try{
            const res = await fetch(
                `https://moneybirdhandlerproxy.azurewebsites.net/api/MoneyBirdProxy`,
                {
                    mode:"cors",
                    method:"POST",
                    body: JSON.stringify({
                        method:"GET",
                        path:`${adminId}/contacts.json`,
                        key: apiKey
                    })
                }
            )
            const json = await res.json()
            console.log({json})
            this.setState({contacts:json})

        }
        catch(e){
            console.log("BOOM!")
            console.log(e)
        }
    }

    save(e){
        e.preventDefault();
        const {apiKey, adminId, contact} = this.state;
        this.microsoftTeams.initialize();
        this.microsoftTeams.settings.setValidityState(true);
        this.microsoftTeams.settings.registerOnSaveHandler((saveEvent) => {
            this.microsoftTeams.settings.setSettings({
                websiteUrl: "https://jp5525.github.io/teams-demo",
                contentUrl: "https://jp5525.github.io/teams-demo",
                entityId: JSON.stringify({apiKey, adminId, contact}),
                suggestedDisplayName: "InvoicesTab"
            });
            saveEvent.notifySuccess();
        });
    }

    render(){
        const {contacts, contact } = this.state
        return (
            
                <Provider theme={themes.teams}>
                    <div style={styles.mainPadding}>
                        <div style={styles.mainTable}>
                            <Header content="MoneyBird API" />
                            <Flex gap="gap.small" vAlign="center" hAlign="start">
                                <FlexItem justifyContent="center">
                                    <form onSubmit={this.submit}>
                                        <Input type="text" placeholder="apiKey" name="apiKey" id="apiKey" onChange={this.handleChange}/> <br />
                                        <Input type="text" placeholder="admin id" name="adminId" id="adminId" onChange={this.handleChange}/> <br />
                                        <Button primary>Fetch Contacts</Button>
                                    </form>
                                </FlexItem>
                            </Flex>
                        </div>
                    </div>

                    <div style={styles.mainSidePadding}>
                        <div style={styles.mainTable}>
                            <Header content="MoneyBird Contact" />
                            <Flex gap="gap.small" vAlign="center" hAlign="start">
                                <FlexItem justifyContent="center">
                                    <form onSubmit={this.save}>
                                        <Dropdown
                                            items={contacts? this.getOptions(): []}
                                            value={contact? contact.content : null}
                                            placeholder='Contacts'
                                            onChange={this.handleContact}
                                            name="contact"
                                            disabled={contacts? false: true}
                                            />
                                        <Button primary>Fetch Invoices</Button>

                                    </form>
                                </FlexItem>
                            </Flex>
                        
                        </div>
                    </div>
                </Provider>
            
            
        )

    }
}

export default Config;
